a {
  cursor: pointer;
}

a:focus,
button:focus {
  outline: none;
}

.map {
  height: 300px;
}

.btn-group-vertical .btn .fa {
  padding-right: 5px;
}

.btn-group-vertical .btn {
  text-align: left;
}

.input-group-btn .btn {
  background: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.input-group-btn .btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

.spinner>div>div {
  display: block;
  margin: auto;
  margin-top: 250px;
}

.required {
  color: red;
}

.centered {
  text-align: center;
}

.left {
  display: block;
  float: left;
}

.right {
  display: block;
  float: right;
}

.panel {
  text-align: left;
}

.progress{
  height: 20px;
  text-align: center;
  border-radius: 3px;
}



